.db-cleaner {
  background-color: #f8f9fa;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  margin: 20px auto;
}

.db-cleaner h3 {
  color: #343a40;
  margin-top: 0;
  margin-bottom: 12px;
}

.db-cleaner p {
  color: #495057;
  margin-bottom: 16px;
}

.clean-button {
  background-color: #dc3545;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 10px 15px;
  cursor: pointer;
  font-weight: 500;
  transition: background-color 0.2s ease;
}

.clean-button:hover {
  background-color: #c82333;
}

.confirm-dialog {
  background-color: #fff3f3;
  border: 1px solid #ffcccc;
  border-radius: 6px;
  padding: 16px;
  margin-top: 16px;
}

.confirm-dialog h3 {
  color: #dc3545;
  margin-top: 0;
  margin-bottom: 12px;
}

.confirm-dialog p {
  margin-bottom: 12px;
}

.confirm-actions {
  display: flex;
  gap: 12px;
  margin-top: 16px;
}

.confirm-button {
  padding: 8px 12px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: 500;
  transition: background-color 0.2s ease;
}

.confirm-button.danger {
  background-color: #dc3545;
  color: white;
}

.confirm-button.danger:hover {
  background-color: #c82333;
}

.confirm-button.cancel {
  background-color: #6c757d;
  color: white;
}

.confirm-button.cancel:hover {
  background-color: #5a6268;
}

.success-message {
  background-color: #d4edda;
  color: #155724;
  padding: 12px;
  border-radius: 4px;
  margin-top: 16px;
  text-align: center;
  font-weight: 500;
}

/* Estilos responsivos */
@media (max-width: 768px) {
  .db-cleaner {
    padding: 15px;
    margin: 15px 0;
  }
  
  .confirm-actions {
    flex-direction: column;
  }
  
  .confirm-button {
    width: 100%;
  }
} 